export default {
    name: "action-button",

    props: {
        actionType: {
            type: String,
            required: true,
        },
        selectedAction: {
            type: String,
            required: true,
        },
        status: {
            type: String,
            required: true,
        },
    },

    computed: {
        selected() {
            return (
                this.selectedAction === this.actionType &&
                this.status !== "idle"
            );
        },
        labelState() {
            return this.selected && this.status === "success"
                ? "copied"
                : "copy";
        },
        label() {
            return `${this.labelState} ${this.actionType}`;
        },
        buttonClasses() {
            return {
                "selected-btn": this.selected && this.status === "success",
                "default-btn": !this.selected,
            };
        },
    },

    methods: {
        handleClick() {
            this.$emit("action-btn-click", {
                actionType: this.actionType,
            });
        },
    },

    filters: {},
};
